.left {
  display: flex;
  flex-direction: row;
  padding: 40px;
  margin: 80px 0px;
  text-align: left;
  flex-wrap: wrap;
  flex-direction: row-reverse; }
  .left .project-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end; }
    .left .project-text ul {
      text-align: right; }

.right {
  display: flex;
  flex-direction: row;
  padding: 40px;
  text-align: right;
  margin: 80px 0px;
  flex-wrap: wrap; }
  .right .project-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start; }
    .right .project-text ul {
      text-align: left; }
  .right img {
    animation-delay: 2s; }

.project-img {
  width: 40%;
  padding: 40px; }
  .project-img img {
    border-radius: 15px 50px;
    width: 100%;
    filter: drop-shadow(0px 14px 28px rgba(63, 119, 194, 0.3));
    animation-name: motion;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: ease-in-out;
    transition: all 1s; }
  .project-img img:hover {
    filter: drop-shadow(0px 14px 28px rgba(101, 126, 158, 0.8)); }

.project-text {
  width: 50%;
  padding: 40px; }
  .project-text h2 {
    color: #363F54;
    font-size: 32px;
    font-weight: bold;
    padding: 20px 0px;
    line-height: 42px;
    animation-name: textmotion;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: ease-in-out;
    animation-delay: .3s; }
  .project-text p {
    margin-left: 20px;
    color: #279AF1;
    line-height: 42px;
    font-size: 17px;
    font-weight: 800; }
  .project-text ul {
    list-style: none;
    color: #677baf;
    line-height: 32px;
    font-size: 15px;
    font-weight: 300; }
  .project-text a {
    background: #8A99C0;
    color: white;
    padding: 10px 20px;
    font-size: 12px;
    border-radius: 20px;
    margin-left: 20px;
    box-shadow: 0px 14px 28px rgba(138, 153, 192, 0.2);
    transition: all 1s; }
  .project-text a:hover {
    box-shadow: 0 8px 16px 0 rgba(138, 153, 192, 0.3), 0 6px 20px 0 rgba(138, 153, 192, 0.25);
    text-decoration: none; }

@media only screen and (max-width: 900px) {
  .project-img {
    width: 100%; }
  .project-text {
    width: 100%; } }

@keyframes motion {
  0% {
    transform: translateY(20px); }
  100% {
    transform: translateY(-20px); } }

@keyframes textmotion {
  0% {
    transform: translateY(5px); }
  100% {
    transform: translateY(-5px); } }
