@keyframes growRight {
  from {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
    visibility: visible;
    width: 0%; }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    width: 100%; } }

.growRight {
  -webkit-animation-name: growRight;
  animation-name: growRight;
  padding-right: 130px; }

.contact-wrapper {
  border-radius: 15px 50px;
  background: #E8C547;
  box-shadow: 10px 14px 28px rgba(232, 197, 71, 0.6);
  padding: 80px 40px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center; }
  .contact-wrapper h1 {
    color: #FFF;
    font-size: 1.6rem; }
  .contact-wrapper a {
    background: #8A99C0;
    padding: 10px 30px;
    border-radius: 20px;
    margin-top: 50px;
    color: #fff;
    font-weight: 500;
    font-size: 0.8rem;
    letter-spacing: 1.3px;
    box-shadow: 0px 14px 28px rgba(138, 153, 192, 0.2);
    transition: all 1s;
    animation-name: buttonmotion;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: ease-in-out;
    animation-delay: .3s; }
  .contact-wrapper a:hover {
    text-decoration: none;
    box-shadow: 0 8px 16px 0 rgba(138, 153, 192, 0.3), 0 6px 20px 0 rgba(138, 153, 192, 0.25); }

@keyframes buttonmotion {
  0% {
    transform: translateY(5px);
    transform: translateX(5px); }
  100% {
    transform: translateY(-5px); } }
